import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({

    state: {
        todos: [
            {
                id: 1,
                title: "Learn JavaScript",
                text: "Learn JavaScript",
                userEmail: "Valera@mail.com",
                userName: "Valera",
            },
            {
                id: 2,
                title: "Learn Vue",
                text: "Learn Vue",
                userEmail: "Vlad@mail.com",
                userName: "Vlad",
            },
            {
                id: 3,
                title: "Play around in JSFiddle",
                text: "Play around in JSFiddle",
                userEmail: "Marina@mail.com",
                userName: "Marina",
            },
            {
                id: 4,
                title: "Build something awesome",
                text: "Build something awesome",
                userEmail: "default@mail.com",
                userName: "Name",
            },
        ],
    },

    mutations: {

        createTodoMutation(state, todo) {
            state.todos.unshift(todo);
        },
        deleteTodoMutation(state, id) {
            state.todos.splice(id, 1);
        },

        initialiseStore(state) {
            // Check if the ID exists
            if (localStorage.getItem('store')) {
                // Replace the state object with the stored item
                this.replaceState(
                    Object.assign(state, JSON.parse(localStorage.getItem('store')))
                );
            }
        }
    },
    actions: {

        createTodo({commit}, payload) {

            commit("createTodoMutation", payload);
        },
        deleteTodo({commit}, payload) {
            commit('deleteTodoMutation', payload);
        },
    },

    getters: {
        todos(state) {
            return state.todos;
        },
    },
});
