<template>
    <div id="content">
        <div class="btn">
            <v-row justify="center">
                <v-dialog v-model="dialog" persistent max-width="600px">
                    <template v-slot:activator="{ on }">
                        <v-btn color="primary" dark v-on="on">Создать пост</v-btn>
                    </template>
                    <v-card>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field
                                                name="userName"
                                                type="text"
                                                v-model="userName"
                                                label="Ваше имя или ник"
                                        >{{ userName }}
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-text-field
                                                name="userEmail"
                                                type="text"
                                                v-model="userEmail"
                                                label="Email"

                                        >{{ userEmail }}
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field
                                                name="title"
                                                type="text"
                                                v-model="title"
                                                label="Введите заголовок"
                                        >{{ title }}
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-textarea label="Text"
                                                    name="text"
                                                    type="text"
                                                    v-model="text"
                                                    color="teal">
                                            <template v-slot:label>
                                                <div>
                                                    <small>{{ text }}</small>
                                                </div>
                                            </template>
                                        </v-textarea>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="dialog = false">Закрыть</v-btn>
                            <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="clear () "
                            >Очистить
                            </v-btn>
                            <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="dialog = false, createCustomTodo(), clear ()"
                            >Отправить
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-row>

        </div>
        <ul>
            <app-default-post v-for="(todo, index) in todos" :todo-prop="todo" :key="index"
                              @delete-todo="deleteTodo(index)"></app-default-post>
        </ul>
    </div>
</template>

<script>
    import appDefaultPost from "./defaultPost";

    export default {
        name: "App",
        data: () => ({
            dialog: false,
            title: "",
            text: "",
            userEmail: "",
            userName: ""
        }),
        components: {
            appDefaultPost: appDefaultPost
        },
        methods: {

            createCustomTodo: function () {
                var todoPost = {
                    id: this.todos.length + 1,
                    userEmail: this.userEmail,
                    userName: this.userName,
                    title: this.title,
                    text: this.text,

                };

                this.$store.dispatch("createTodo", todoPost), console.log("create2");
            },


            deleteTodo(id) {

                this.$store.dispatch('deleteTodo', id);
            },
            clear() {

                this.userEmail = ''
                this.userName = ''
                this.title = ''
                this.text = ''
            },

        },

        computed: {
            todos: {
                get() {
                    return this.$store.getters.todos;
                },

                set() {
                    this.$store.dispatch("getTodos");
                }
            }
        }
    }


</script>

<style>
    .btn {
        margin: 3em;
    }

    .form {
        width: 20%;
        margin: 20px;
        flex-direction: column;
        background-color: #e3e3fe;
        padding: 10px;
        border: solid 2px #e3e3e3;
        display: flex;
        justify-content: space-around;
    }

    .name {
        text-align: center;
        margin-bottom: 20px;
    }

    .tittle {
        text-align: center;
        margin-bottom: 20px;
        font-size: 1.5em;
    }

    .post {
        margin: 2em;
    }

</style>
